import {
  typeSwitch,
  typePicker,
  typeRadio,
  typeLocate,
  typeFile,
  typeField,
  typeDate,
  typeArea,
  typeFormConfirm,
  typeCheckbox,
  typeDesc,
  typeSmsCode
} from '../../components/form-item/config'

export default {
  label: {
    label: '',
    type: 'field'
  },
  type: (item) => {
    let options = []
    if (typeArea.includes(item.type)) {
      options = typeArea
    } else if (typeDate.includes(item.type)) {
      options = typeDate
    } else if (typeField.includes(item.type)) {
      options = typeField
    } else if (typeFile.includes(item.type)) {
      options = typeFile
    } else if (typeRadio.includes(item.type)) {
      options = typeRadio
    } else if (typePicker.includes(item.type)) {
      options = typePicker
    } else if (typeSwitch.includes(item.type)) {
      options = typeSwitch
    } else if (typeLocate.includes(item.type)) {
      options = typeLocate
    } else if (typeFormConfirm.includes(item.type)) {
      options = typeFormConfirm
    } else if (typeSmsCode.includes(item.type)) {
      options = typeSmsCode
    } else if (typeDesc.includes(item.type)) {
      options = typeDesc
    } else if (typeCheckbox.includes(item.type)) {
      options = typeCheckbox
    }
    return {
      label: '',
      type: 'radio',
      options
    }
  },
  required: {
    label: '',
    type: 'switch'
  },
  field: {
    label: '',
    type: 'field'
  },
  placeholder: {
    label: '',
    type: 'field'
  },
  errMsg: {
    label: '',
    type: 'field'
  },
  ruler: {
    label: '',
    type: 'field'
  },
  dataUrl: {
    label: '',
    type: 'field'
  },
  fieldMap: {
    label: '',
    type: 'field'
  },
  jsonp: {
    label: '',
    type: 'field'
  },
  format: {
    label: '',
    type: 'field'
  },
  options: {
    label: '',
    type: 'option'
  },
  params: {
    label: '',
    type: 'option'
  },
  info: {
    label: '',
    type: 'field'
  },
  relation: {
    label: '',
    type: 'option2'
  },
  minDate: {
    label: '',
    type: 'field',
    placeholder: '2020/01/25'
  },
  maxDate: {
    label: '',
    type: 'field',
    placeholder: '2020/01/25'
  },
  toNowDays: {
    label: '(x)',
    type: 'field'
  },
  isMinNow: {
    label: '',
    type: 'switch'
  },
  isMaxNow: {
    label: '',
    type: 'switch'
  },
  id: {
    label: 'id',
    type: 'field'
  },
  displayInAdmin: {
    label: '',
    type: 'switch'
  },
  displayInMobile: {
    label: '',
    type: 'switch'
  },
  blockFieldIndex: {
    label: '',
    type: 'field'
  },
  text: {
    label: '',
    type: 'field'
  }
}
